<style type="text/css">
  @import '../../assets/orderDetail.css';
  /*tab栏*/
  .order-msg{
    margin-top: 10px;
  }
  .order-msg.el-tabs--card>.el-tabs__header .el-tabs__item.is-active{
    background-color:#409EFF;
    color:#fff;
  }
  .order-msg.el-tabs--card>.el-tabs__header{
    border-bottom: none;
  }
  .order-msg.el-tabs--card .el-tabs__header{
    margin-bottom: 0;
  }
  .order-msg.el-tabs--card .el-tabs__header .el-tabs__nav {
    border-bottom: 1px solid #e4e7ed;
    background-color: #fff;
  }
  .bgcf{
    padding: 10px;
    background-color: #fff;
  }

  .l-con-order {
    flex: 1;
  }

  /*暂无订单*/
  .no-order{
    line-height: 60px;
    text-align: center;
  }

  /*每个订单*/
  .everyOrder{
    background-color: #fff;
    margin-bottom: 20px;
  }
  .everyOrder>div{
    padding:10px;
  }
  .msg-detail {
    float: right;
    cursor: pointer;
  }

  /*右边订单日志内容开始*/
  .r-con-order {
    flex: 0 0 280px;
    width: 280px;
    margin-left: 10px;
  }
  .r-con-order>div{
    padding: 20px 10px;
    max-height: 200px;
    background-color: #fff;
    margin-bottom: 20px;
  }
  .rc-p{
    padding-left: 5px;
    line-height: 32px;
  }
</style>
<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="search-form-wraper">
        <el-select class="search-input" v-model="storeNo" filterable placeholder="请选择门店" size="small" @change="doSearchHandel" clearable>
          <el-option
            v-for="item in storeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>

    <!-- 搜索结果 -->
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick" class="order-msg">
      <el-tab-pane name="taday">
        <span slot="label">今日待自提 {{tadayNum}}</span>
      </el-tab-pane>
      <el-tab-pane name="tomorrow">
        <span slot="label">明日待自提 {{tomorrowNum}}</span>
      </el-tab-pane>
      <el-tab-pane name="yesterday">
        <span slot="label">超时未提 {{yesterdayNum}}</span>
      </el-tab-pane>
    </el-tabs>
    <div class="o-detail-wraper" v-loading="loading">
      <div class="l-con-order" v-if="orderMesg.length > 0">
        <div v-for="item in orderMesg" :key="item.orderId" class="everyOrder">
          <!-- 详情信息第一条 -->
          <div class="lc-msg-st">
            <p class="p-name">{{item.platformStr}}</p>
            <p class="p-ot-msg">
              <span>预售自提</span>
              <span style="padding:0 2px;">|</span>
              <span v-if="item.pickStartTime && item.pickEndTime">要求 {{item.pickDate}} {{item.pickStartTime}}~{{item.pickEndTime}} 自提</span>
              <span v-else>要求 {{item.pickDate}} 自提</span>
              <span style="padding:0 2px;">|</span>
              <span>下单时间 {{item.orderTime}}</span>
              <span style="padding:0 2px;">|</span>
              <span>订单号 {{item.orderId}}</span>
            </p>
            <p class="o-status">
              {{item.orderStatusName}}
            </p>
          </div>
          
          <!-- 详情信息第二条 客户 -->
          <div class="lc-msg-2p1i">
            <div class="icon-w">
              <i class="fa fa-user-circle" aria-hidden="true"></i>
            </div>
            <div class="msg-2p">
              <p>
                <span>{{item.userName}}</span>
                <span>&nbsp;&nbsp;{{item.userPhoneNum}}</span>
              </p>
              <p>提货地点：{{item.pickAddr}}</p>
            </div>
          </div>

          <!-- 详情信息第三条 店铺地址 -->
          <div class="lc-msg-2p1i">
            <div class="icon-w">
              <i class="fa fa-university" aria-hidden="true"></i>
            </div>
            <div class="msg-2p">
              <p>
                <span>{{item.storeNo}}</span>
                <span>{{item.storeName}}</span>
              </p>
              <p>等待门店备货</p>
            </div>
          </div>

          <!-- 详情信息第四条 配送 -->
          <div class="lc-msg-2p1i">
            <div class="icon-w">
              <i class="fa fa-motorcycle" aria-hidden="true"></i>
            </div>
            <div class="msg-2p">
              <p>
                <span>自提订单无需配送</span>
              </p>
              <p>等待顾客自提</p>
            </div>
          </div>

          <!-- 详情信息第五条 -->
          <div class="lc-msg-2p1i">
            <div class="icon-w">
              <i class="fa fa-shopping-basket" aria-hidden="true"></i>
            </div>
            <div class="msg-2p">
              <p>
                商品信息
              </p>
              <p>
                <span style="color:#e6a23c;padding-right:10px;">订单备注：</span>
                <span>
                  {{item.remark || '暂无'}}
                </span>
                <span class="msg-detail" v-if="item.msgDetail == true" @click="item.msgDetail = false">
                  收起
                </span>
                <span class="msg-detail" v-else  @click="item.msgDetail = true">
                  展开
                </span>
              </p>
              
              <!-- 商品列表 -->
              <div class='goods-mesg' v-if="item.msgDetail == true">
                <div class="g-m-item" v-for="(item, idx) of item.detailVos" :key="idx">
                  <p>{{item.productId}} - {{item.skuName}}</p>
                  <p>
                    <span v-show="item.dispPrice && item.dispPrice != item.payPrice && item.dispPrice != 0 && item.dispPrice != '0.00'">
                      ￥{{item.dispPrice || '0.00'}}
                    </span>
                  </p>
                  <p>￥{{item.skuPrice || '0.00'}}</p>
                  <p>x{{item.skuQty || 0}}</p>
                  <p>￥{{item.skuAmount || '0.00'}}</p>
                </div>
              </div>
              
              <!-- 合计 -->
              <div class='goods-mesg'>
                <div class="gm-2p">
                  <p>商品总计</p>
                  <p>￥{{item.orderAmount || '0.00'}}</p>
                </div>
                <div class="seller-d-a">
                  <p class="sda-tit">
                    <span>优惠总金额</span>
                    <span>￥{{item.discountAmount || '0.00'}}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
          
          <!-- 详情信息第六条 收益 -->
          <div class="lc-msg-2p1i last-2p1i">
            <div class="icon-w">
              <i class="fa fa-jpy" aria-hidden="true"></i> 
            </div>
            <div class="msg-2p">
              <p class="gm-2p">
                <span>本单预计收入</span>
                <span style="padding-right: 10px;font-weight: bold;">￥{{item.orderAmount || '0.00'}}</span>
              </p>
              <p>
                <span>本单顾客实际支付</span>
                <span style="padding-left:10px;">￥{{item.payAmount || '0.00'}}</span>
              </p>
            </div>
          </div>
          
        </div>

      </div>
      <div class="l-con-order bgcf" v-else>
        <div class="no-order">
          没有查询到您想要的数据
        </div>
      </div>
      <div class="r-con-order">
        <div>
          <p class="rc-t">今日自提单合计</p>
          <p class="rc-p">订单数： {{tadayOrderTotal.orderCount || '0'}} 笔</p>
          <p class="rc-p">已自提： {{tadayOrderTotal.pickedCount || '0'}} 笔</p>
          <p class="rc-p">销售金额： {{tadayOrderTotal.totalOrderAmount || '0.00'}} 元</p>
        </div>
        <div>
          <p class="rc-t">明日自提单合计</p>
          <p class="rc-p">订单数： {{tomorrowOrderTotal.orderCount || '0'}} 笔</p>
          <p class="rc-p">商品数： {{tomorrowOrderTotal.productCount || '0'}} 个</p>
          <p class="rc-p">销售金额： {{tomorrowOrderTotal.totalAmount || '0.00'}} 元</p>
        </div>
      </div>
      
    </div>

  </div>
</template>
<script>
import * as utils from '@/utils/utils'

const orderProcessing = {
  // 有权限门店选择
  selectStore: '/plateform/a/store/condition/selectStore',
  // 获取待自提单据列表
  getOrderList: '/plateform/a/mall/waitPickOrder/getOrderList',
  // 获取自提单合计
  getTodayCountInfo: '/plateform/a/mall/waitPickOrder/getTodayCountInfo',
  // 明日自提合计
  tomorrowTotal: '/plateform/a/mall/orderProduct/tomorrowTotal'
}


export default {
  data() {
    return {
      loading: false,
      pageNo: 1,
      pageSize: 20,
      total: 0,
      tableData: [],
      storeOptions: [],
      storeNo: '', // 门店编码
      activeName: 'taday',
      tadayNum: 0,
      tomorrowNum: 0,
      yesterdayNum: 0,
      tadayOrder: [],
      tomorrowOrder: [],
      yesterdayOrder: [],
      orderMesg: [], // 订单列表
      tadayOrderTotal: [],
      tomorrowOrderTotal: []
    }
  },
  created() {
    // 获取门店
    this.getSelectStore()
    // 查询订单列表 
    this.doSearchHandel()
  },
  mounted() {
  },
  components: {
  },
  methods: {
    /**
     * [getSelectStore 获取门店]
     * @return {[type]} [description]
     */
    getSelectStore() {
      this.$http.post(orderProcessing.selectStore, {DIFFCITY: true}).then(res => {
        if(res.code === 0) {
         if(res.data && res.data.length) {
            let optionsArr = []
            for(let item of res.data) {
              let optionItem = {
                value: item.erpStoreNo,
                label: item.erpStoreNo +'-'+ item.erpStoreName
              }
              optionsArr.push(optionItem)
            }

            this.storeOptions = optionsArr
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(获取门店列表):", e.message)
      })
    },
    /**
     * [doSearchHandel 搜索获取列表]
     * @return {[type]} [description]
     */
    doSearchHandel(param) {
      this.loading = true
      let pickToday = utils.timestampToTimeNoHMS(new Date().getTime())
      let pickTomorrow = utils.timestampToTimeNoHMS(new Date().getTime() + (86400000 * 1))
      if(param == 'tomorrow') {
        // 明日待自提
        this.$http.post(orderProcessing.getOrderList, {pickDate: pickTomorrow, storeNo: this.storeNo, storeNo: this.storeNo, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.loading = false
            if(res.data && res.data.result && res.data.result.length) {
              let result = res.data.result
              result.forEach((item, i) => {
                item.pickDate = utils.timestampToTimeNoHMS(item.pickDate)
                if(item.pickStartTime && item.pickEndTime) {
                  item.pickStartTime = utils.timestampToTime(item.pickStartTime).slice(11, 16)
                  item.pickEndTime = utils.timestampToTime(item.pickEndTime).slice(11, 16)
                }
                item.orderTime = utils.timestampToTimeYMDHM(item.orderTime)
                item.orderStatusName = '明日待自提'
                item.msgDetail = false
              })
              this.tomorrowOrder = result
            }else{
              this.tomorrowOrder = []
            }
            let name = this.activeName + 'Order'
            this.orderMesg = this[name]
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          console.log("提示(明日待自提):", e.message)
        })
      }else if(param == 'yesterday') {
        // 超时未提
        this.$http.post(orderProcessing.getOrderList, {pickDate: pickToday, dateOldFlag: true, storeNo: this.storeNo, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.loading = false
            if(res.data && res.data.result && res.data.result.length) {
              let result = res.data.result
              result.forEach((item, i) => {
                item.pickDate = utils.timestampToTimeNoHMS(item.pickDate)
                if(item.pickStartTime && item.pickEndTime) {
                  item.pickStartTime = utils.timestampToTime(item.pickStartTime).slice(11, 16)
                  item.pickEndTime = utils.timestampToTime(item.pickEndTime).slice(11, 16)
                }
                item.orderTime = utils.timestampToTimeYMDHM(item.orderTime)
                item.orderStatusName = '超时未提'
                item.msgDetail = false
              })
              this.yesterdayOrder = result
            }else{
              this.yesterdayOrder = []
            }

            let name = this.activeName + 'Order'
            this.orderMesg = this[name]
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          console.log("提示(超时未提):", e.message)
        })
      }else{
        // 今日待自提
        this.$http.post(orderProcessing.getOrderList, {pickDate: pickToday, storeNo: this.storeNo, DIFFCITY: true}).then(res => {
          if(res.code === 0) {
            this.loading = false
            if(res.data && res.data.result && res.data.result.length) {
              let result = res.data.result
              result.forEach((item, i) => {
                item.pickDate = utils.timestampToTimeNoHMS(item.pickDate)
                if(item.pickStartTime && item.pickEndTime) {
                  item.pickStartTime = utils.timestampToTime(item.pickStartTime).slice(11, 16)
                  item.pickEndTime = utils.timestampToTime(item.pickEndTime).slice(11, 16)
                }
                item.orderTime = utils.timestampToTimeYMDHM(item.orderTime)
                item.orderStatusName = '今日待自提'
                item.msgDetail = false
              })
              this.tadayOrder = result
            }else{
              this.tadayOrder = []
            }

            let name = this.activeName + 'Order'
            this.orderMesg = this[name]
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          console.log("提示(今日待自提):", e.message)
        })
      }
      // 今日待自提合计
      this.$http.post(orderProcessing.getTodayCountInfo, {pickDate: pickToday, storeNo: this.storeNo, DIFFCITY: true}).then(res => {
        if(res.code === 0) {
          if(res.data) {
            this.tadayOrderTotal = res.data
            this.tadayNum = res.data.todayOrderCount || 0
            this.tomorrowNum = res.data.tomorrowOrderCount || 0
            this.yesterdayNum = res.data.timeOutOrderCount || 0

          }else{
            this.tadayOrderTotal = []
            this.tadayNum = 0
            this.tomorrowNum = 0
            this.yesterdayNum = 0
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(今日待自提合计):", e.message)
      })
      // 明日自提合计
      this.$http.post(orderProcessing.tomorrowTotal, {storeNo: this.storeNo, DIFFCITY: true}).then(res => {
        if(res.code === 0) {
          if(res.data) {
            this.tomorrowOrderTotal = res.data
          }else{
            this.tomorrowOrderTotal = []
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        console.log("提示(明日待自提合计):", e.message)
      })
    },
    /**
     * [handleClick 点击tab]
     * @return {[type]} [description]
     */
    handleClick(tab, event) {
      let name = tab.name + 'Order'
      this.orderMesg = this[name]
      this.doSearchHandel(tab.name)
    }
  }
}
</script>


